.image {
    width: 240px;
    border-bottom: 1px solid #e8e8e8; 
    border-radius: 5px; 
    height: 280px;
    /* top: 10px;
	left: 10px; */
	/* z-index: 1; */
}

.review {
    position: relative;;
    top: 0px;
    right: 0px;
    left: 10px;
    width: 30px;
    height: 30px;
    z-index: 4;
}

.review .score {
    position: relative;
    top: -40px;
    left: 90px;
    z-index: 2;
    height: 20px;
    width: 20px;
    /* color: rgb(12, 12, 12); */
    font-size: 25px;
    padding: 8px;
    /* background-color: rgb(184, 179, 179); */
    /* border-radius: 50%; */
    display: flexbox;
    text-align: center;
    font-weight: bold;
}

.review:hover {
    cursor: pointer; 
}

.p-des {
    padding: 0;
    padding-top: 10px;
}
.p-des p {
    padding: 0 10px;
    margin: 5px 0 15px 0;
    font-size: 15px;
    white-space: nowrap; 
    overflow: hidden;
    text-overflow: ellipsis;
}

.card {
    float: left;
    width: 240px;
    display: flex;
    flex-direction: column;
    text-align: left;
    margin: 15px;
    margin-bottom: 25px;
    background: white;
    border-radius: 5px;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}
.card:hover {
    transition: 0.4s;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.button-grp button {
    color: #555f61;
    width: 50%;
    height: 36px;
    border: none;
    outline: none;
    background: none;
    font-size: 16px;
    font-weight: 600;
    border: 1px solid white;
    border-top: 1px solid #e8e8e8;  
}
.button-grp button:hover {
    border: 1px solid #e8e8e8;
    cursor: pointer;
}